.tabs {
	width: 100%;
	padding: 100px 0;
	@media screen and (max-width: $sm) {
		padding: 50px 0;
	}
}

.tabs__content {
	@include flex-container(row, space-around, center, wrap);
	padding: 50px 0;
	width: 100%;
	@media screen and (max-width: $sm) {
		padding: 30px 0;
	}
}


.tabs__wrap {
	@include flex-child(900px);
	border-radius: $radius-sm;
	padding: 20px;
}

.tabs__holder-wrap {
	width: 100%;
}

.tab {
	@include flex-container(row, space-around, center, wrap);
	position: relative;
}

.tabs__cta {
	border-bottom: $border-light;
	@media screen and (max-width: $sm) {
		@include flex-container(row, space-around, center, nowrap);
	}
}

.tab__btn {
	color: $ternary-light;
	opacity: $opacity-sm;
	position: relative;
	&::after {
		background-color: $ternary-light;
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 2px;
		opacity: 0;
	}
	@include animation {
		transition: $transition-medium;
	}
	@media screen and (max-width: $sm) {
		padding: 10px 12px;
	}
}

.tab__btn--active {
	opacity: 1;
	&::after {
		opacity: 1;
	}
}

.tab__text {
	@include flex-child(100%);
	font-size: 16px;
	text-align: center;
}

.tab__img {
	@include flex-child(40%);
	position: relative;
	margin-bottom: 30px;
	@media screen and (max-width: $sm) {
		@include flex-child(100%);
	}
}